<script>
import mockingData from 'mock/news_release.json'
import moment from 'moment'

export default {
  data(){
      return{
        currentCategory: 'ALL',
        filterYear: 'ALL',
      }
  },
  filters: {
    dateFilter: function (value) {
      return moment(value).format('D MMMM YYYY');
    }
  },
  computed: {
    tableData: function () {
      let retval = []
      if (this.currentCategory == 'ALL') {
        retval = mockingData;
      } else {
        retval = mockingData.filter(x => {
          return x.category == this.currentCategory;
        });
      }
      return retval;
    }
  },
  methods: {
    filterByYear: function (year) {
      return this.tableData.filter(x => moment(x.date).format('YYYY') == year);
    },
    getYears: function () {
      let retval = this.tableData.map(x => moment(x.date).format('YYYY'));
      return retval.filter(function(item, pos) {
          return retval.indexOf(item) == pos;
        });
    },
    setNav: function (navName) {
      this.currentCategory = navName;
      return false;
    },
    getCategoryClass: function (category) {
      switch (category) {
        case 'ALL':
          return 'cate-all';
        case 'プレスリリース':
          return 'cate-press-release';
        case 'コンテンツ／コラム':
          return 'cate-content-column';
        case 'メディア':
          return 'cate-media';
        case '投資情報':
          return 'cate-investment-info';
        case '採用／その他':
          return 'cate-recruitment-others';
      }
    }

  }
}
</script>

